<template>
  <div>
    <div id="summary">
      <el-row class="serach_box">
        <el-col class="text-right">
          <el-button @click="addColumn">增加交期</el-button>
          <el-button @click="addRow">增加货号</el-button>
          <el-button @click="remove">删除</el-button>
        </el-col>
      </el-row>
      <!-- 表单区 -->
      <div id="middle">
        <el-table :data="formData.plan_Common_Details" @select="rowclick">
          <el-table-column label="江苏新萌芽智能纺织有限公司    计划表 "
            ><el-table-column label="订单号"
              ><el-table-column type="selection" width="80"></el-table-column
            ></el-table-column>
            <el-table-column>
              <template slot="header" slot-scope="scope">
                <el-input
