                />
              </template>
              <el-table-column label="照片" width="120"></el-table-column
            ></el-table-column>
            <el-table-column label="审核"
              ><el-table-column label="机种" width="120">
                <template slot-scope="scope">
                  <el-input
                    v-model="scope.row.machineType"
                    clearable
                  ></el-input>
                </template> </el-table-column
            ></el-table-column>
            <el-table-column>
              <template slot="header" slot-scope="scope">
                <el-input
                  :key="scope.index"
                  class="input-box"
                  v-model="formData.reviewer"
                  size="mini"
                  placeholder="输入"
                /> </template
              ><el-table-column label="针数(N)" width="120">
                <template slot-scope="scope">
                  <el-input
                    v-model="scope.row.needleNumber"
                    :key="scope.index"
                    clearable
                  ></el-input>
                  <!-- <div>
                    <el-input v-model="scope.row.number" clearable></el-input>
                  </div>
                  <div>
                    <el-input v-model="scope.row.number" clearable></el-input>
                  </div>
                  <div>
                    <el-input v-model="scope.row.number" clearable></el-input>
                  </div> -->
                </template>
              </el-table-column></el-table-column
            >
            <el-table-column label="用料" width="120">
              <template slot-scope="scope">
                <el-input v-model="scope.row.materiaType" clearable></el-input>
              </template>
            </el-table-column>
            <el-table-column label="缝头方式" width="120">
              <template slot-scope="scope">
                <el-input v-model="scope.row.sewMethod" clearable></el-input>
              </template>
            </el-table-column>
            <el-table-column label="功能性" width="120">
              <template slot-scope="scope">
                <el-input v-model="scope.row.functions" clearable></el-input>
              </template>
            </el-table-column>
            <el-table-column label="后道处理" width="120">
              <template slot-scope="scope">
                <el-input v-model="scope.row.postProcess" clearable></el-input>
              </template>
            </el-table-column>
            <el-table-column label="备注1" width="120">
              <template slot-scope="scope">
                <el-input v-model="scope.row.comment1" clearable></el-input>
              </template>
            </el-table-column>
            <el-table-column label="双/组（pack）" width="120">
              <template slot-scope="scope">
                <el-input
                  v-model="scope.row.amountPerPack"
                  clearable
                ></el-input>
              </template>
            </el-table-column>
            <el-table-column label="组/数（pack）" width="120">
              <template slot-scope="scope">
                <el-input v-model="scope.row.packPerBox" clearable></el-input>
              </template>
            </el-table-column>
            <el-table-column label="总数（组/pack）" width="120">
              <template slot-scope="scope">
                <el-input
                  v-model="scope.row.amountPerPack"
                  clearable
                ></el-input>
              </template>
            </el-table-column>
            <el-table-column label="总数（双）" width="120">
              <template slot-scope="scope">
                <el-input v-model="scope.row.totalPack" clearable></el-input>
              </template>
            </el-table-column>
            <el-table-column label="尺寸" width="120">
              <template slot-scope="scope">
                <el-input v-model="scope.row.size" clearable></el-input>
              </template>
            </el-table-column>
          </el-table-column>
          <el-table-column style="padding: 0px" label="交期货号">
            <div style="height: 32px; line-height: 32px">PO号</div>
            <div style="height: 32px; line-height: 32px">交期</div>
            <div style="height: 32px; line-height: 32px">pack数</div>
            <div style="height: 32px; line-height: 32px">双数</div>
          </el-table-column>
          <el-table-column
            label="交期货号"
            v-for="(item, index) in max"
            :key="index"
          >
            <template slot-scope="scope">
              <el-input
                v-model="scope.row.deliveryTimes[item - 1].poNumber"
              ></el-input>
              <el-input
                v-model="scope.row.deliveryTimes[item - 1].deliveryTime"
              ></el-input>
              <el-input
                v-model="scope.row.deliveryTimes[item - 1].packAmount"
              ></el-input>
              <el-input
                v-model="scope.row.deliveryTimes[item - 1].sockAmount"
              ></el-input>
            </template>
          </el-table-column>
          <!-- <el-table-column v-for="item in items" :key="item.index">
            <template slot="header" slot-scope="scope">
              PO号{{ item }}:<el-input
                class="input-box"
                v-model="search"
                size="mini"
                placeholder="输入"
              />
            </template>
            <el-table-column label="交期">
              <template slot="header" slot-scope="scope">
                交期{{ item }}:<el-input
                  class="input-box"
                  v-model="search"
                  size="mini"
                  placeholder="输入"
                />
              </template>
              <el-table-column label="pack数" width="80">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.number" clearable></el-input>
                </template>
              </el-table-column>
              <el-table-column label="双数" width="80">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.number" clearable></el-input>
                </template>
              </el-table-column>
            </el-table-column>
          </el-table-column> -->
          <el-table-column label="额外样品数量">
            <el-table-column label="尺寸样">
              <template slot-scope="scope">
                <el-input
                  v-model="scope.row.sizeSampleAmount"
                  clearable
                ></el-input>
              </template>
            </el-table-column>
            <el-table-column label="大货样">
              <template slot-scope="scope">
                <el-input
                  v-model="scope.row.productSampleAmount"
                  clearable
                ></el-input>
              </template>
            </el-table-column>
            <el-table-column label="检测样">
              <template slot-scope="scope">
                <el-input
                  v-model="scope.row.testSampleAmount"
                  clearable
                ></el-input>
              </template>
            </el-table-column>
          </el-table-column>
          <el-table-column label="备注2">
            <template slot-scope="scope">
              <el-input v-model="scope.row.comment2" clearable></el-input>
            </template>
          </el-table-column>
        </el-table>
        <el-row class="input-index">
          <div>备注三</div>
          <el-input
            clearable
            v-model="formData.comment3"
            class="input-box"
          ></el-input>
        </el-row>
        <el-row class="table-footer">
          <el-col :span="2">/</el-col>
          <el-col :span="2" v-for="(item, index) in footerItems" :key="index">{{
            item.label
          }}</el-col>
          <el-col :span="8">备注四</el-col>
        </el-row>
        <el-row class="table-footer">
          <el-col :span="2">计划日期</el-col>
          <el-col :span="2" v-for="(item, index) in footerItems" :key="index"
            ><el-input v-model="formData[item.prop]"></el-input
          ></el-col>
          <el-col :span="8"
            ><el-input v-model="formData.comment4"></el-input
          ></el-col>
        </el-row>
      </div>
      <!-- 提交区 -->
      <div class="footer">
        <el-checkbox v-model="checked">不对外发布</el-checkbox>
        <el-button @click="removeDisplay = true" type="info">取消</el-button>
        <el-button @click="submitDialogDisplay = true" type="primary"
          >提交</el-button
        >
      </div>
      <!-- 提交弹框 -->
      <el-dialog
        width="30%"
        title="提交申请"
        :visible.sync="submitDialogDisplay"
      >
        <span>将提交对当前页面的更改</span>
        <span slot="footer" class="dialog-footer">
          <el-button type="info" @click="submitDialogDisplay = false"
            >取 消</el-button
          >
          <el-button type="primary" @click="submit">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 取消对话框 -->
      <el-dialog width="30%" title="取消修改" :visible.sync="removeDisplay">
        <span>将取消当前页面的更改，所有未提交的信息将会丢失</span>
        <span slot="footer" class="dialog-footer">
          <el-button type="info" @click="removeDisplay = false"
            >取 消</el-button
          >
          <el-button type="primary">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import {
  GetSelCommonPlans,
  InsertOrUpdateCommonPlan,
  DeleteCommonPlanDetails,
} from "network/average.js";
import { arraySort } from "service/dateUtil.js";

import { TableItem, PoItem } from "service/Business/newAverage.js";

export default {
  data() {
    return {
      checked: false,
      deliverItems: [
        [
          {
            packAmount: "",
            sockAmount: "",
            poNumber: "",
            deliveryTime: "",
          },
        ],
        [
          {
            packAmount: "",
            sockAmount: "",
            poNumber: "",
            deliveryTime: "",
          },
        ],
      ],
      tableData: [
        {
          number: "2",
          samplenumber: "DC20-Q3",
          salesman: "路人甲",
          client: "路人乙",
          typename: "花",
          picture: "123.kb",
          typemachine: "K140",
          needle: "140",
          material: "20*15(平铺) 20*15(平铺)",
          data: "2020.11.18",
          status: "已完成",
          operation: false,
          url:
            "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        },
        {
          number: "2",
          samplenumber: "DC20-Q3",
          salesman: "路人甲",
          client: "路人乙",
          typename: "花",
          picture: "123.kb",
          typemachine: "K140",
          needle: "140",
          material: "20*15(平铺) 20*15(平铺)",
          data: "2020.11.18",
          status: "已完成",
          operation: true,
          url:
            "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        },
        {
          number: "1",
          samplenumber: "DC20-Q3",
          salesman: "路人甲",
          client: "路人乙",
          typename: "花",
          picture: "123.kb",
          typemachine: "K140",
          needle: "140",
          material: "20*15(平铺) 20*15(平铺)",
          data: "2020.11.18",
          status: "已完成",
          operation: true,
          url:
            "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        },
      ],
      selectData: [],
      formData: {
        planId: 0,
        orderId: "",
        creationTime: "",
        produceGroup: "",
        salesManId: 0,
        salesManName: "",
        customerId: 0,
        customerName: "",
        reviewer: "",
        plan_Common_Details: [],
        sampleConfirmTime: "",
        rawMateriaCalcTime: "",
        rawMateriaPrepareTime: "",
        samplePreProduceTime: "",
        produceTime: "",
        sewingTime: "",
        packingTime: "",
        comment3: "",
        comment4: "",
        planState: "",
      },
      headerItems: [
        { label: "订单号", value: "customerName" },
        { label: "制单日期", value: "customerName" },
        { label: "生产小组", value: "customerName" },
        { label: "业务员", value: "customerName" },
        { label: "客户", value: "customerName" },
        { label: "审核", value: "customerName" },
      ],
      footerItems: [
        { label: "样品确认", prop: "sampleConfirmTime" },
        { label: "大货算料", prop: "rawMateriaCalcTime" },
        { label: "大货备料", prop: "rawMateriaPrepareTime" },
        { label: "产前样", prop: "samplePreProduceTime" },
        { label: "生产", prop: "produceTime" },
        { label: "缝头、定型", prop: "sewingTime" },
        { label: "检品包装", prop: "packingTime" },
      ],
      deleteData: null,
      items: [1, 2],
      search: "11",
      removeDisplay: false,
      submitDialogDisplay: false,
      max: 1,
      emptyItem: null,
    };
  },
  created() {
    this.inital();
  },
  methods: {
    //初始化
    inital() {
      if (this.$route.query.iid == undefined) {
      } else {
        let data = this.$route.query.iid;
        GetSelCommonPlans(data).then((res) => {
          if (res.success) {
            this.formData = res.result;
            this.tableDataInital(this.formData.plan_Common_Details);
          } else {
          }
        });
      }
    },
    //添加数据编号
    addIndex(data) {
      data.forEach((item, index) => {
        item.index = index;
      });
    },
    //找到最大值
    getMax(data) {
      data.forEach((item) => {
        let len = item.deliveryTimes.length;
        /* console.log("12121"); */
        this.max = len > this.max ? len : this.max;
      });
    },
    //添加数据预处理
    addDeliver(data) {
      data.forEach((item) => {
        let i = item.deliveryTimes.length;
        for (i; i < this.max; i++) {
          this.emptyItem = new PoItem();
          item.deliveryTimes[i] = this.emptyItem;
        }
      });
    },
    //数据初始化
    tableDataInital(data) {
      this.addIndex(data);
      this.getMax(data);
      this.addDeliver(data);
    },
    //增加交期
    addColumn() {
      this.max = this.max + 1;
      this.addDeliver(this.formData.plan_Common_Details);
    },
    //增加货号
    addRow() {
      const data = this.formData.plan_Common_Details;
      let obj = new TableItem();
      data.push(obj);
      this.addDeliver(data);
    },
    //筛选空数据
    filterData() {
      //深拷贝
      let data = JSON.parse(JSON.stringify(this.formData.plan_Common_Details));
      data.forEach((item) => {
        item.deliveryTimes = item.deliveryTimes.filter((item1) => {
          if (
            item1.deliveryTime != "" ||
            item1.poNumber != "" ||
            item1.sockAmount != "" ||
            item1.packAmount != ""
          ) {
            return true;
          } else {
            return false;
          }
        });
      });
      console.log(data);
      return data;
    },
    /* 获得选中行数据 */
    rowclick(selection) {
      this.selectData = selection;
    },
    //删除按钮
    remove() {
      const data = this.selectData;
      //判断是否为空
      if (data.length == 0) {
        this.$message.error("请先选择删除项！");
      } else {
        let arr = new Array(data.length);
        let arr1 = new Array(data.length);
        data.forEach((item, index) => {
          arr[index] = item.planDetailId;
          arr1[index] = item.index;
        });
        arr1 = arraySort(arr1);
        //对界面数据进行删除
        arr1.forEach((item) => {
          this.formData.plan_Common_Details.splice(item, 1);
        });
        this.deleteData = arr;
      }
    },
    //删除操作
    delete() {
      DeleteCommonPlanDetails(this.deleteData).then((res) => {
        if (res.success) {
          this.$message.success("删除成功");
        }
      });
    },
    //提交操作
    submit() {
      if (this.deleteData) {
        this.delete();
      } else {
        console.log("1111");
      }
      this.formData.plan_Common_Details = this.filterData();
      InsertOrUpdateCommonPlan(this.formData).then((res) => {
        if (res.success) {
          this.$message.success("提交成功");
        }
      });
    },
    //取消
    cancel() {
      this.$router.go(-1);
    },
    getSummaries(param) {
      const sums = [];
      var sum1 = 0;
      for (var i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].number == "") continue;
        sum1 = sum1 + parseInt(this.tableData[i].number);
      }
      sums[0] = "总价";
      sums[1] = sum1;
      return sums;
    },
  },
};
</script>

<style scoped>
.header {
  margin-bottom: 10px;
}
.input-box {
  width: 65% !important;
}
.input-index {
  text-align: left;
  font-size: 14px;
  border: 1px solid #313131;
  border-style: none solid solid solid;
}
.input-index > div:first-child {
  display: inline-block;
  width: 79px;
  line-height: 32px;
  border-right: 1px solid;
  text-align: center;
}
.input-index .el-input {
  width: 80% !important;
  border-left: 1px solid !important;
}
.table-footer {
  text-align: left;
  font-size: 14px;
}
.table-footer > div {
  display: inline-block;
  line-height: 32px;
  border: 1px solid #313131;
  border-style: none solid solid none;
  text-align: center;
}
.table-footer > div:first-child {
  border-left: 1px solid;
}
.footer {
  margin-top: 10px;
  text-align: right;
}
.footer .el-checkbox {
  margin: 0 30px;
}
</style>