import {request} from './request'

//获得计划单
export async function GetAllCommonPlans(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/GetAllCommonPlans',
        data:data
    })
}

//新增或更新计划单
export async function InsertOrUpdateCommonPlan(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/InsertOrUpdateCommonPlan',
        data:data
    })
}

//获得单个计划表信息
export async function GetSelCommonPlans(data){
    return await request({
        url:'/api/BusinessUnit/GetSelCommonPlans',
        params:{planId:data}
    })
}

//删除计划单
export async function DeleteCommonPlan(data){
    return await request({
        method:'DELETE',
        url:'/api/BusinessUnit/DeleteCommonPlan',
        data:data
    })
}

//单个计划表删除
export async function DeleteCommonPlanDetails(data){
    return await request({
        method:'DELETE',
        url:'/api/BusinessUnit/DeleteCommonPlanDetails',
        data:data
    })
}

//批量更新状态
export async function UpdateCommonPlanState(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/InsertOrUpdateCommonPlans',
        data:data
    })
}